import React, { Component } from "react";
import img from "./img/images.png";
import robotIcon from "./img/robot-icon.jpg";
import plus from "./img/plus.svg";
import minus from "./img/minus.svg";
import rotateL from "./img/rotateL.svg";
import rotateR from "./img/rotateR.svg";
import "./App.scss";

class App extends Component {
  state = {
    rotate: 0,
    width: 400,
    robotWidth: 5,
    positionRobotT: 76,
    positionRobotL: 51,
  };

  zoomIn = () => {
    if (this.state.width < 600) {
      this.setState((prevState) => {
        return { width: prevState.width + 50 };
      });
      this.setState((prevState) => {
        return {
          robotWidth: prevState.robotWidth + 0.5,
          positionRobotT: prevState.positionRobotT + 10,
          positionRobotL: prevState.positionRobotL + 6,
        };
      });
    }
  };

  zoomOut = () => {
    if (this.state.width > 400) {
      this.setState((prevState) => {
        return { width: prevState.width - 50 };
      });
      this.setState((prevState) => {
        return {
          robotWidth: prevState.robotWidth - 0.5,
          positionRobotT: prevState.positionRobotT - 10,
          positionRobotL: prevState.positionRobotL - 6,
        };
      });
    }
  };

  rotateR = () => {
    this.setState((prevState) => {
      return { rotate: prevState.rotate + 25 };
    });
  };

  rotateL = () => {
    this.setState((prevState) => {
      return { rotate: prevState.rotate - 25 };
    });
  };

  rotateReset = () => {
    this.setState({ rotate: 0, width: 400 });
  };

  handleChangeRight = (e) => {
    const { value } = e.currentTarget;
    this.setState((prevState) => {
      return { positionRobotL: value };
    });
  };

  handleChangeBottom = (e) => {
    const { value } = e.currentTarget;
    this.setState((prevState) => {
      return { positionRobotT: value };
    });
  };

  render() {
    const currentRotate = `rotate(${this.state.rotate}deg)`;
    const currentWidth = `${this.state.width}px`;
    const robotWidth = `${this.state.robotWidth}%`;
    const positionRobotT = `${this.state.positionRobotT}px`;
    const positionRobotL = `${this.state.positionRobotL}px`;

    return (
      <div className="container">
        <div>
          <form className="form">
            <label className="label">
              Right
              <input
                className="input"
                type="number"
                value={this.state.positionRobotL}
                onChange={this.handleChangeRight}
                min="51"
                max="344"
                step="1"
              />
            </label>
            <label className="label">
              Bottom
              <input
                className="input"
                type="number"
                value={this.state.positionRobotT}
                onChange={this.handleChangeBottom}
                min="76"
                max="263"
                step="1"
              />
            </label>
          </form>
        </div>
        <div
          className="thumb"
          style={{ transform: currentRotate, width: currentWidth }}
        >
          <img src={img} alt="floor layout" className="layout"></img>
          <img
            src={robotIcon}
            className="icon-robot"
            alt="icon robot"
            style={{
              top: positionRobotT,
              left: positionRobotL,
              width: robotWidth,
            }}
          ></img>
        </div>
        <div className="btns">
          <button className="plus" onClick={this.zoomIn}>
            <img src={plus} alt="plus"></img>
          </button>
          <button className="minus" onClick={this.zoomOut}>
            <img src={minus} alt="minus"></img>
          </button>
          <button className="rotateR" onClick={this.rotateR}>
            <img src={rotateR} alt="rotateR"></img>
          </button>
          <button className="rotateReset" onClick={this.rotateReset}>
            Reset
          </button>
          <button className="rotateL" onClick={this.rotateL}>
            <img src={rotateL} alt="rotateL"></img>
          </button>
        </div>
      </div>
    );
  }
}

export default App;
